.instructions {
    min-height: 100vh;
    background-color: #ffffff;
    padding-top: 40px;
    padding-bottom: 40px;

    .switch {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        p {
            text-align: center;
            font-size: 24px;
            color: $color-estetic;
            font-family: $ff-mon, serif;
            text-align: center;
            padding-bottom: 40px;

            @include _md {
                font-size: 20px;
            }
        }
    }

    &-switch-button {
        display: flex;
        justify-content: center;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        background-color: $color-yellow;
        margin-bottom: 10px;
        transition: 0.5s;

        &.active {
            border: 8px solid $color-estetic;
            transform: scale(1.2);
        }


        @include _sm {
            width: 80px;
            height: 80px;
        }

        a {
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        img {
            max-width: 80px;

            @include _sm {
                max-width: 50px;
            }
        }
    }

    &-title {
        font-size: 24px;
        font-family: $ff-mon, serif;
        color: $color-estetic;
        padding-bottom: 10px;

        @include _sm {
            font-size: 20px;
        }
    }

    &-text {
        font-size: 18px;
        color: #6d6d6d;
        padding-bottom: 15px;

        &.bold {
            font-weight: 700;
            padding-bottom: 0;
        }
    }
}