.about-main {
    background: rgb(230,230,230);
    background: linear-gradient(90deg, rgba(230,230,230,1) 0%, rgba(221,221,221,1) 0%, rgba(255,255,255,1) 100%);
    min-height: 70vh;
    padding-top: 120px;
    padding-bottom: 90px;

    @include _lg {
        padding-top: 140px;
        padding-bottom: 20px;
    }

    .col-lg-4 {
        @include _lg {
            margin-bottom: 180px;
        }

        @include _sm {
            margin-bottom: 100px;
        }
    }
    
    .about-card {
        font-family: $ff-mon, serif;
        background-color: #ffffff;
        border-radius: 20px;
        height: 100%;
        padding-left: 20px;
        padding-right: 20px;
        padding-bottom: 50px;
        box-shadow: 4px 10px 20px rgba(0, 0, 0, 0.2);
        transition: 0.3s all;

        &:hover, &:focus {
            transform: scale(1.010);

            .about-card-titles::after {
                width: 55%;
            }
        }

        

        img {
            width: 100%;
            margin-top: -20%;
            padding-bottom: 10%;
        }

        &-titles {
            color: $color-estetic;
            padding-bottom: 28px;
            margin-bottom: 30px;

            &::after {
                content: "";
                display: block;
                position: absolute;
                height: 5px;
                background: $color-estetic;
                width: 30%;
                margin-top: 20px;
                border-radius: 5px;
                transition: 1s;
            }

            h2 {
                font-size: 48px;
                font-weight: 300;

                @include _sm {
                    font-size: 36px;
                }
            }
        }

        &-subtitle {
            font-size: 18px;
            color: #3b3e3d;
        }

        &-link {
            font-size: 24px;
            text-decoration: none;
            color: $color-estetic;
        }
    }
}