.panangin-about {
    min-height: 70vh;
    background-color: #fcfcfb;
    padding-top: 70px;
    padding-bottom: 50px;

    &-img {
        width: 100%;
        padding-top: 50px;

        img {
            width: 100%;
        }
    }

    &-choose {
        display: flex;
        align-items: center;
        img {
            max-width: 56px;
            max-height: 56px;
            margin-right: 5px;
        }

        &-text {
            color: #6d6d6d;
            font-size: 16px;
            margin-top: 25px;
        }
    }

    &-content {
        position: relative;
        background-color: #ffffff;
        width: 100%;
        border-radius: 15px;
        padding-top: 125px;
        padding-bottom: 50px;
        padding-right: 20px;
        padding-left: 125px;
        margin-top: 60px;
        box-shadow: 4px 10px 20px rgba(0, 0, 0, 0.2);

        @include _sm {
            padding-left: 40px;
        }

        &:hover {
            .panangin-about-content-round-logo {
                transform: scale(1.1);
            }

            .panangin-about-content-title {
                &::after {
                    width: 40%;
                }
            }
        }

        &-title {
            font-size: 36px;
            font-family: $ff-mon, serif;
            color: $color-estetic;
            font-weight: 300;

            &::after {
                content: "";
                display: block;
                position: absolute;
                height: 5px;
                background: $color-estetic;
                width: 30%;
                margin-top: 20px;
                border-radius: 5px;
                transition: 0.4s;
            }

            @include _md {
                font-size: 30px;
            }
        }

        &-round-logo {
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: $color-yellow;
            left: 30%;
            top: -50px;
            border: 5px solid #fcfcfb;
            box-shadow: 0 0 59px rgba(117, 116, 115, 0.16);
            border-radius: 50%;
            width: 140px;
            height: 140px;
            transition: 0.4s;
            img {
                top: 50%;
                left: 50%;
                max-width: 80px;
            }
        }
    }

    &-benefits {
        text-align: left;
        padding-top: 30px;
        text-align: center;
        color: #3b3e3d;
        font-size: 20px;
        font-weight: 700;

        &-list {
            &-item::before {
                content: "\2022";
                color: $color-estetic;
                font-weight: bold;
                display: inline-block;
                width: 20px;
            }
        }

    }
}
