.footer {
    padding-top: 50px;
    padding-bottom: 50px;
    background-color: #e6e6e6;

    
    .gedeon-footer-logo {
        width: 70%;

        @include _md {
            width: 100%;
        }
    }

    &-content {
        border-top: 5px solid rgb(19, 1, 121);
        border-bottom: 5px solid rgb(19, 1, 121);
        text-align: center;
        font-weight: 800;
        color: rgb(19, 1, 121);
        font-size: 16px;

        @include _sm {
            font-size: 14px;
        }
    }
}