.panangin-recomendations {
    padding-top: 50px;
    padding-bottom: 50px;

    .row {
        align-items: center;
    }

    img {
        width: 100%;
    }

    .recomendations-text {
        &-list {
            padding-left: 30px;
            
            &-item {
                color: #6d6d6d;
                padding-bottom: 15px;
                font-size: 16px;
                font-weight: 700;

                &::before {
                    content: "\2022";
                    color: $color-yellow;
                    font-weight: bold;
                    display: inline-block;
                    width: 25px;
                }
            }
        }
    }
}