.btn, 
a.btn,
input.btn,
button.btn {
    border: unset;
    line-height: 1;

    display: inline-flex;
    justify-content: center;
    align-items: center;

    padding: unset;
    transition: all 0.3s;
    font-size: 16px;

    &-toggler {
        // p10
        opacity: 1 !important;
        z-index: 3;
        position: absolute;
        left: -60%;
        text-decoration: none;
        padding-top: 10px;
        outline: none;
        border: none;


        &:focus {
            outline: none;
            border: none;
            box-shadow: none;
        }

        &.active {
            left: 10vw;
        }
        

        .hamburger-inner,
        .hamburger-inner::after,
        .hamburger-inner::before {
            background-color: $color-estetic !important;

            
        }
    }

    &:hover, &:focus {
        outline: none;
        border: none;
    }
}
    