.panangin-title {
    font-size: 36px;
    color: $color-estetic;
    padding-bottom: 35px;
    font-family: $ff-mon, serif;
}
.panangin-heart {
    background-color: #fcfcfb;
    min-height: 70vh;
    padding-top: 50px;
    padding-bottom: 50px;

    &-how {
        &-list {
            &-item {
                font-size: 16px;
                color: #6d6d6d;
                padding-bottom: 20px;
            }
        }
    }

    .heart {
        background: url("../../../../media/heart_bg2.png") right top / cover;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img {
            width: 50%;
            height: 55%;
            animation: beat 2s linear infinite;

            @include _lg {
                height: 85%;
            }

            @include _lg {
                animation: none;
            }

            @keyframes beat {
                0% {
                    transform: scale(1);
                }
                25% {
                    transform: scale(0.9);
                }
                50% {
                    transform: scale(1.1);
                }
                75% {
                    transform: scale(1);
                }
                100% {
                    transform: scale(1.2);
                }
            }
        }

        
        .k-icon {
            position: absolute;
            z-index: 1;
            text-align: center;
            bottom: 0;
            left: 0;
            animation: k-fly 1s linear infinite;

            img {
                width: 30%;
            }

            @keyframes k-fly {
                0% {
                    transform: scale(1);
                }
                50% {
                    transform: scale(1.2);
                }
                100% {
                    transform: scale(1);
                }
            }
        }

        .mg-icon {
            position: absolute;
            z-index: 1;
            text-align: center;
            top: 0;
            right: 0;
            animation: mg-fly 1s linear infinite;

            img {
                width: 30%;
            }

            @keyframes mg-fly {
                0% {
                    transform: scale(1);
                }
                50% {
                    transform: scale(1.2);
                }
                100% {
                    transform: scale(1);
                }
            }
        }
    }
}
