.home-heart {
    min-height: 70vh;
    background: url('../../../../media/heart_bg.png') center bottom / cover;
    padding-top: 60px;
    padding-bottom: 60px;

    &-title {
        position: relative;
        text-align: center;
        font-size: 48px;
        font-family: $ff-mon, serif;
        color: $color-estetic;
        font-weight: 300;
        padding-bottom: 30px;

        &::after {
            content: "";
            display: block;
            position: absolute;
            height: 1px;
            background: rgba(0, 0, 0, 0.205);
            width: 15%;
            border-radius: 5px;
            top: 50%;
        }

        &::before {
            content: "";
            display: block;
            position: absolute;
            height: 1px;
            right: 0;
            background: rgba(0, 0, 0, 0.205);
            width: 15%;
            border-radius: 5px;
            top: 50%;
        }

        @include _sm {
            font-size: 36px;
        }
    }

    .did-know {
        padding-bottom: 40px;

        &-little {
            color: #6d6d6d;
            font-size: 20px;
            font-family: $ff-mon, serif;
            font-style: italic;
            padding-bottom: 20px;

            @include _md {
                font-size: 16px;
            }
        }

        &-big {
            font-size: 38px;
            color: $color-estetic;

            @include _md {
                font-size: 26px;
            }
        }
    }

    .heart-card {
        padding: 20px 30px;
        background-color: #ffffff;
        border: 1px solid $color-estetic;
        border-radius: 10px;
        margin-bottom: 20px;

        &-title {
            color: $color-estetic;
            font-size: 32px;
            padding-bottom: 15px;
        }

        &-list {
            &-item {
                color: #6d6d6d;

                &::before {
                    content: "\2022";
                    color: $color-estetic;
                    font-weight: bold;
                    display: inline-block;
                    width: 20px; 
                }
            }
        }
    }

    .big-heart {
        width: 100%;
        display: flex;
        justify-content: center;
        position: relative;
        .heart-icon {
            width: 50%;
            height: 20%;
            animation: beat 2s linear infinite;
            z-index: 2;
            
            @include _md {
                animation: none;
            }

            @keyframes beat {
                0% {
                    transform: scale(1);
                }
                25% {
                    transform: scale(0.9);
                }
                50% {
                    transform: scale(1.1);
                }
                75% {
                    transform: scale(1);
                }
                100% {
                    transform: scale(1.2);
                }
            }
        }
        .k-icon {
            position: absolute;
            z-index: 1;
            text-align: center;
            bottom: 0;
            left: 0;
            animation: k-fly 2s linear infinite;

            img {
                width: 30%;
            }

            @keyframes k-fly {
                0% {
                    transform: scale(1);
                }
                50% {
                    transform: scale(1.2);
                }
                100% {
                    transform: scale(1);
                }
            }
        }

        .mg-icon {
            position: absolute;
            z-index: 1;
            text-align: center;
            top: 0;
            right: 0;
            animation: mg-fly 2s linear infinite;

            img {
                width: 30%;
            }

            @keyframes mg-fly {
                0% {
                    transform: scale(1);
                }
                50% {
                    transform: scale(1.2);
                }
                100% {
                    transform: scale(1);
                }
            }
        }

    }
}