.home-header {
    min-height: 90vh;
    display: flex;
    align-items: center;
    padding-bottom: 20px;

    @include _md {
        background: linear-gradient(rgba(0, 0, 0, 0), #f6e6cc);
    }

    

    @include _md {
        align-items: start;
    }

    .owl-nav {
        width: 100%;
        position: absolute;
        display: flex;
        justify-content: space-between;
        top: 40%;
        font-size: 100px;
        font-weight: 300;
        color: $color-estetic;

        .owl-prev, .owl-next {
            outline: none;
        }

        @include _lg {
            display: none;
        }
    }

    .owl-dots {
        width: 100%;
        position: absolute;
        display: flex;
        justify-content: center;

        @include _lg {
            top: 37%;
            justify-content: flex-end;
        }

        @include _sm {
            top: 28%;
        }

        .owl-dot {
            background: none;
            background-color: transparent;
            height: 10px;
            width: 10px;
            border: 1px solid $color-estetic !important;
            margin-right: 10px;
            border-radius: 50%;
            outline: none;

            &.active {
                background-color: $color-estetic;
            }

        }
    }

    .header-carousel-img {
        display: flex;
        flex-direction: column;
        align-items: center;

        img {
            padding-bottom: 100px;
        }
    }

    

        
    }


.header-carousel-content {
    position: relative;
    background-color: #fff;
    width: 100%;
    height: 100%;
    border-radius: 15px;
    margin-top: 90px;
    padding-top: 60px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 50px;
    box-shadow: 20px;

    @include _md {
        margin-top: 100px;
    }

    &-title {
        font-size: 40px;
        font-family: $ff-mon, serif;
        color: $color-estetic;
        padding-top: 40px;
        font-weight: 700;

        &::after {
            content: "";
            display: block;
            position: absolute;
            height: 5px;
            background: $color-estetic;
            width: 40%;
            margin-top: 20px;
            border-radius: 5px;
        }

        @include _md {
            font-size: 30px;
        }
    }

    &-list {
        padding-top: 30px;
        font-size: 20px;
        font-weight: 300;
        padding-bottom: 20px;

        &-item {
            &::before {
                content: "\2022";
                color: $color-estetic;
                font-weight: bold;
                display: inline-block;
                width: 20px; 
            }
        }
    }
}

.round-logo {
    position: absolute;
    background-color: $color-yellow;
    left: 30%;
    top: -80px;
    border-radius: 50%;
    padding: 25px 25px;
    color: $color-estetic;
    img {
        max-width: 110px;
        max-height: 110px;
    }
}