.panangin-advantages {
    min-height: 40vh;
    padding-top: 50px;

    .col-lg-4 {
        margin-bottom: 40px;
    }

    &-card {
        width: 100%;
        height: 100%;
        background-color: #ffffff;
        box-shadow: 0 0 50px rgba(117, 116, 115, 0.16);
        border-radius: 10px;
        position: relative;

        &-title {
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            font-size: 24px;
            color: #3a3a3a;
            padding: 20px 0;
            border-bottom: 1px solid #3a3a3a;
            font-family: $ff-mon, serif;
            height: 120px;
        }

        &-spin {
            position: relative;
            -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
            transition:transform 1s;
            background-color: $color-yellow;
            min-height: 170px;
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
        }

        .front-side,
        .back-side {
            position: absolute;
            top: 0;
            left: 0;
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
        }

        .front-side {
            background-color: $color-yellow;
            width: 100%;
            padding: 20px;
            text-align: center;
            
            img {
                width: 100px;
            }
        }
        .back-side {
            transform:rotateY(180deg);
            background-color: $color-yellow;
            font-size: 16px;
            padding: 20px;
            color: #6d6d6d;
        }

        &:hover &-spin {
            transform:rotateY(180deg);
        }
    }
}
