.header {
    position: relative;
    overflow: hidden;
    
    @import './navbar.scss';
}

.past-header {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 46px;

    &-title {
        font-size: 60px;
        color: $color-estetic;
        text-align: center;
        padding-bottom: 36px;
        margin-bottom: 32px;
        font-family: $ff-mon, serif;

        @include _sm {
            font-size: 40px;
        }

        &::after {
            content: "";
            display: block;
            position: absolute;
            right: 45%;
            left: 45%;
            height: 5px;
            background: $color-estetic;
            width: 10%;
            border-radius: 5px;
            margin-top: 32px;
        }
    }

    &-subtitle {
        font-size: 36px;
        font-weight: 300;
        color: $color-estetic;

        @include _sm {
            font-size: 30px;
        }
    }
}


.navbar-wrapper {
    position: fixed;
    height: 100vh;
    width: 40vw;
    background-color: #ffffff;
    z-index: 4;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    right: -50vw;
    transition:  0.4s;

    @include _sm {
        width: 60vw;
        right: -80vw;
    }


    @include _md{
        padding: 20px 15px;
    }
    
    &.active {
        right: 0;
        
        @include _sm {
            right: 0;
        }
    }
}

.navbar-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-top: 50px;
}
    
.navbar-list-item {
    color: $color-grey;
    font-size: 25px;
    position: relative;
    font-family: $ff-mon, sans-serif;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-right: 10px;
    text-transform: uppercase;
    
    @include _md {
        font-size: 16px;
    }

    
}

.navbar-link {
    color: $color-grey;
}
                        
.navbar-link:hover {
    color: $color-estetic;
    text-decoration: none;
}

[data-navbar-toggler] {
    // d:none
    display: inline-block;
    // ml:a
    margin-left: auto;

    @include _lg {
        display: inline-block;
    }

    
}

.header-link {
    color: #ffffff;
    font-family: $ff-mon, serif;
    font-size: 20px;
    background-color: $color-estetic;
    padding: 15px 30px;
    border-radius: 25px;

    &:hover {
        color: #ffffff;
    }
}